	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>2019.4 - 2019.6</p>\n</blockquote>\n<div>\n          <div\n            class=\"gatsby-resp-iframe-wrapper\"\n            style=\"padding-bottom: 74.79166666666667%; position: relative; height: 0; overflow: hidden;\"\n          >\n            <iframe src=\"https://giphy.com/embed/j0SitLaYtVvEV8soe9\" frameborder=\"0\" class=\"giphy-embed\" allowfullscreen style=\"\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 100%;\n            height: 100%;\n          \"></iframe><p><a href=\"https://giphy.com/gifs/music-machine-learning-gamification-j0SitLaYtVvEV8soe9\">via GIPHY</a></p>\n          </div>\n          </div>\n<h2 id=\"about\"><a href=\"#about\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>About</h2>\n<p>I’m exploring the idea of using gamification to make the musical machine\nlearning algorithms more understandable and fun. The first project is Sornting.\n“RUNN” is the 2nd one.</p>\n<p>Basically, it is “run” (side-scrolling game) + “RNN”. The user can play with the\ngenerated output without any prior knowledge about musical machine learning.\nEveryone can just play the game and enjoy the generated music. Also, they can\nkeep the good vibe (the background beat) by controlling their avatar, or not.\nAll the levels are generated realtime with a MusicRNN model.</p>\n<h2 id=\"links\"><a href=\"#links\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Links</h2>\n<p><a href=\"https://vibertthio.com/runn\">> Play It</a></p>\n<p><a href=\"https://github.com/vibertthio/runn\">> Source Code</a></p>","frontmatter":{"title":"Runn"}}},"pathContext":{"slug":"/projects/runn/"}}

